import { createApp } from 'vue/dist/vue.esm-bundler';
import App from './App.vue'
import router from './router'
import axios from 'axios'

const gameTitleRow = {
    props:["rowData"],
    template : `
        <div class="card" style="width: 200px; margin:10px 10px 10px 10px">
			<router-link to="#" v-on:click.prevent.stop="pushGame(this.rowData.id)">
				<img :src="iconUrl" class="card-img-top" alt="..." style="border-bottom:1px solid gray">
				<div class="card-body">
					<h6 class="card-title">{{ this.rowData.name }}</h6>
					<h6 class="card-subtitle mb-2 text-muted">配信数：{{ this.rowData.order}}</h6>
				</div>
			</router-link>
        </div>`,
	data: function () {
			return {
			}
	},
	computed : {
		iconUrl : function () {
            let iconUrl = this.rowData.icon;
            iconUrl = iconUrl.replace('{width}', '200');
            iconUrl = iconUrl.replace('{height}', '266');
			return iconUrl;
		},
	},
	methods : {
		pushGame (gameId) {
			console.log(gameId);
			this.$router.push('/view/' + gameId);
		}
	}
}


createApp(App)
.component("game-title-row", gameTitleRow)
.use(router, axios)
.mount('#app')
