<template>
<br>
<div class="top container-fluid" style="text-align:center">
  <h3 class="fontBase">ゲームタイトル検索</h3>
  <input type="text" style="width:70%; margin:auto" :value="searchWord" @input="updateList" class="form-control" placeholder="ゲーム名（英語表記）を入力してください" aria-label="Game Title" aria-describedby="search_button">
  <hr style="height: 2px; background-color:white; border:none;">
  <h1 class="fontBase">{{ searchLabel }}</h1>
  <div style="width:90%; display:flex; flex-wrap:wrap; justify-content:center; margin:auto">
    <game-title-row v-for="(rowData, index) in dispDataList" :key="index" v-bind:rowData='rowData'></game-title-row>
  </div>
  <button v-show="this.isNext == true" type="button" v-on:click="pushNext">もっと見る</button>
  
  <transition name="fade">
    <button type="button" style="position:fixed; bottom:15px; right:15px" v-if="show" @click="moveTop">上へ</button>
  </transition>
</div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Top',
  components: {
  },
  data() {
    return {
      langIndex : 0,
      btnOn : "btn-primary",
      btnOff : "btn-secondary",
      pageIndex : 1,
      searchWord : "",
      isNext : false,
      searchLabel : "Top Games",
      
      dispDataList: [],
      filterDataList : [],
      allRowDataList: [],

      show : false,
    };
  },
  mounted () {
    window.addEventListener('scroll', () => {
        console.log(window.scrollY);
        this.show = (window.scrollY > 150);

    });
  },
  methods : {
    updateList : function (e) {
      this.searchWord = e.target.value;
      if (this.searchWord == "") {
        //検索ワードなし
        this.filterDataList = this.allRowDataList;
        this.searchLabel = "Top Games (" + this.filterDataList.length + ")";
      } else {
        //検索ワードあり
        this.filterDataList = [];
        for (let i = 0; i < this.allRowDataList.length; i++) {
          const gameData = this.allRowDataList[i];
          if (gameData["name"].toUpperCase().indexOf(this.searchWord.toUpperCase()) > -1) {
            //一致
            this.filterDataList.push(gameData);
          }
        }
        this.searchLabel = "Search '" + this.searchWord + "' (" + this.filterDataList.length + ")";
      }
      console.log(this.searchLabel)
      this.pageIndex = 1;
      this.dispList();
    },
    dispList : function () {
      let max = 40 * this.pageIndex;
      if (max > this.filterDataList.length) {
        max = this.filterDataList.length;
        this.isNext = false;
      } else {
        this.isNext = true;
      }

      this.dispDataList = [];
      for (let i = 0; i < max; i++) {
        this.dispDataList.push(this.filterDataList[i]);          
      }
    },
    pushNext : function () {
      this.pageIndex = this.pageIndex + 1;
      this.dispList();
    },
    moveTop : function () {
      window.scrollTo(0, -100);   // スクロール位置を移動

      // const duration = 1000;  // 移動速度（1秒で終了）
      // const interval = 25;    // 0.025秒ごとに移動
      // const step = -window.scrollY / Math.ceil(duration / interval); // 1回に移動する距離
      // console.log(window.scrollY + " " + step + " " + Math.ceil(duration / interval));
      // let flg = true;
      // const timer = setInterval(() => {
      //     if (flg) {
      //       console.log("A" + window.scrollY);
      //     }
      //     window.scrollBy(0, step);   // スクロール位置を移動
      //     if (flg) {
      //       console.log("B" + window.scrollY);
      //     }
      //     flg = false;
      //     if(window.scrollY <= 0) {
      //         clearInterval(timer);
      //     }
      // }, interval);
    },
    pushLang : function (index) {
      this.langIndex = index;
      console.log(index);
    },
    getGameList : function () {
      axios.get(this.$root.SERVER_PATH + 'getGames')
      .then((response) => {
        let resData = response.data;
        console.log(resData);

        this.allRowDataList = [];
        this.allRowDataList = resData["retList"];
        this.filterDataList = this.allRowDataList;
        this.searchLabel = "Top Games (" + this.filterDataList.length + ")";
        this.pageIndex = 1;
        this.dispList();
      }).catch((error) => {
        console.log(error.response);
      });
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.getGameList();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.getGameList();
    });
  },
}
</script>
