<template>
<br>
<div class="schedule container-fluid">
  <button type="button" v-on:click="pushTimer">{{timerLabel}}</button>
  <h1>{{ second }}</h1><br>
  <button type="button" v-on:click="getGames">ゲーム取得</button><br><br><br>
  <button type="button" v-on:click="test">テスト</button>
</div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Schedule',
  components: {
  },
  data() {
    return {
      second : 0,
      isTimer : false,
      timerLabel : "スタート",

      timerID : null,
    };
  },
  methods : {
    test : function () {
        let params = new URLSearchParams();
        params.append('userId', "abcdefg");
        params.append('gameId', "511224");
        console.log("ゲーム一覧取得");
        axios.post(this.$root.SERVER_PATH + 'getStreams', params)
        .then((response) => {
          let resData = response.data;
          console.log(resData);
          console.log("ゲーム一覧取得終了");
        }).catch((error) => {
          console.log(error.response);
        });
    },
    pushTimer : function () {
      if (!this.isTimer) {
        //タイマースタート
        this.timerLabel = "ストップ"
        this.isTimer = true;
        this.timerID = setInterval(this.updateTime, 1000);
      } else {
        //タイマーストップ
        this.timerLabel = "スタート"
        this.isTimer = false;
        clearInterval(this.timerID);
      }
    },
    updateTime : function () {
      //現在時刻取得
      var date = new Date();
      this.second = date.getSeconds();

      if (this.second == 0) {
        //配信一覧を取得
        console.log("取得" + date);

        axios.get(this.$root.SERVER_PATH + 'updateStreams')
        .then((response) => {
          let resData = response.data;
          console.log(resData);
        }).catch((error) => {
          console.log(error.response);
        });
      }
    },
    getGames : function () {
        console.log("ゲーム一覧取得");
        axios.get(this.$root.SERVER_PATH + 'updateGames')
        .then((response) => {
          let resData = response.data;
          console.log(resData);
          console.log("ゲーム一覧取得終了");
        }).catch((error) => {
          console.log(error.response);
        });
    },
    updateList : function (e) {
      this.searchWord = e.target.value;
      if (this.searchWord == "") {
        //検索ワードなし
        this.searchLabel = "Top Games";
        this.filterDataList = this.allRowDataList;
      } else {
        //検索ワードあり
        this.searchLabel = "Search '" + this.searchWord + "'";

        this.filterDataList = [];
        for (let i = 0; i < this.allRowDataList.length; i++) {
          const gameData = this.allRowDataList[i];
          if (gameData["name"].toUpperCase().indexOf(this.searchWord.toUpperCase()) > -1) {
            //一致
            this.filterDataList.push(gameData);
          }
        }
      }
      console.log(this.searchLabel)
      this.pageIndex = 1;
      this.dispList();
    },
    dispList : function () {
      let max = 40 * this.pageIndex;
      if (max > this.filterDataList.length) {
        max = this.filterDataList.length;
        this.isNext = false;
      } else {
        this.isNext = true;
      }

      this.dispDataList = [];
      for (let i = 0; i < max; i++) {
        this.dispDataList.push(this.filterDataList[i]);          
      }
    },
    pushNext : function () {
      this.pageIndex = this.pageIndex + 1;
      this.dispList();
    },
    pushLang : function (index) {
      this.langIndex = index;
      console.log(index);
    },
    getGameList : function () {
      axios.get(this.$root.SERVER_PATH + 'getGames')
      .then((response) => {
        let resData = response.data;
        console.log(resData);

        this.allRowDataList = [];
        this.allRowDataList = resData["retList"];
        this.filterDataList = this.allRowDataList;
        console.log(this.filterDataList.length);
        this.pageIndex = 1;
        this.dispList();
      }).catch((error) => {
        console.log(error.response);
      });
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.getGameList();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.getGameList();
    });
  },
}
</script>
